import {Component, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanFileServiceConfigService} from "@generated/vsan-file-service-config-service";
import {DisableFileServicesModalContext} from "@navigation/context/disable-file-services-modal-context";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {SrMessageType} from "@service/screen-reader-announcer.service";

@Component({
   templateUrl: "disable-file-services.html",
   selector: "vsan-disable-file-services",
   styleUrls: ["disable-file-services.scss"],
})
export class DisableFileServicesComponent implements OnInit {

   @ErrorMessage()
   error: string;

   @LoadingIndicator()
   loading: boolean;

   clusterName: string;
   clusterRef: ManagedObject;
   shareCount: number;

   constructor(private fileServicesConfigService: VsanFileServiceConfigService) {
   }

   @Loader({srMessage: "vsan.services.fileservices.wizard.disable.title", srMessageType: SrMessageType.PAGE_TITLE})
   async ngOnInit(): Promise<void> {
      const context: DisableFileServicesModalContext = VsanUiUtils.getModalContext() as DisableFileServicesModalContext;
      this.clusterName = context.clusterName;
      this.clusterRef = context.clusterRef;
      this.shareCount = context.fileShareCount;
   }

   @Loader()
   async confirm(): Promise<void> {
      const taskRef: any = await this.fileServicesConfigService.disableFileService(this.clusterRef);
      VsanUiUtils.closeModalDialog(taskRef);
   }
}
